import React, { Component } from 'react';
import { Home } from './home';
import { BrowserRouter as Route, HashRouter, Link, Switch } from "react-router-dom";
import { ContactMe } from './contact-me';

export class Main extends Component {

    render() {
        return (
            <HashRouter>
                <Switch>
                    <Link to="/contact-me" component={ContactMe} /> 
                    <Link to="/" component={Home} />
                </Switch>
            </HashRouter>
        );
    }
}
